import React from 'react';
import Button from "react-bootstrap/es/Button";
import Modal from "react-bootstrap/es/Modal";
import NavItem from "react-bootstrap/es/NavItem";
import Form from "react-bootstrap/es/Form";
import FormGroup from "react-bootstrap/es/FormGroup";
import Col from "react-bootstrap/es/Col";
import {Checkbox} from "react-bootstrap";
import FormControl from "react-bootstrap/es/FormControl";
import ControlLabel from "react-bootstrap/es/ControlLabel";
import {Nav} from 'react-bootstrap';
import AuthService from './AuthService';

import axios from 'axios';
import './Login.css';

class Login extends React.Component {
    constructor(props, context) {
        super(props, context);
        this.handleHide = this.handleHide.bind(this);
        this.getCurrentUser = this.getCurrentUser.bind(this);
        this.logout = this.logout.bind(this);
        this.state = {
            show: false,
            username: ""
        };
    }

    handleHide() {
        this.setState({show: false});
    }

    handleShow() {
        this.setState({show: true});
    }

    onChange = (e) => {
        this.user({[e.target.name]: e.target.value});
    };

    getCurrentUser = () => {
        fetch("security/getUsername").then(response => response.json()).then(json => {
            this.setState({username: json.username});
        }).catch();
    }

    componentWillMount() {
        this.getCurrentUser();
    }

    logout() {
        fetch("/logout", {method: "post"}).then(() => window.location = "/").catch("");
    }


    render() {
        if (this.state.username !== "" && this.state.username != undefined) {
            return(<p onClick={() => this.logout()} href="#">{this.state.username}</p>);
        }
        return (

            <div className="modal-container">

                <p onClick={() => this.setState({show: true})} href="#">LOGIN</p>

                <Modal
                    show={this.state.show}
                    onHide={this.handleHide}
                    container={this}
                    aria-labelledby="contained-modal-title">

                    <Modal.Header closeButton>
                        <Modal.Title id="contained-modal-title">
                            Login
                        </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <form name="f" action="/login" method="post">
                            <fieldset>
                                <legend>Please Login</legend>
                                <label htmlFor="username" id={"usernameLabel"}>Username</label>
                                <input type="text" id="username" name="username"/>
                                <br/>
                                <label htmlFor="password" id={"passwordLabel"}>Password</label>
                                <input type="password" id="password" name="password"/>
                                <br/>
                                <br/>
                                <div className="form-actions">
                                    <button type="submit" className="btn btn-default"
                                            onClick={"document.getElementsByTagName('form')[0].submit();"}>Log in
                                    </button>
                                </div>
                            </fieldset>
                        </form>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button onClick={this.handleHide}>Close</Button>
                    </Modal.Footer>
                </Modal>
            </div>
        );
    }
}

export default Login;


